* {
  letter-spacing: 0.035rem;
  font-family: 'Roboto', sans-serif;
  color: #404040;
  cursor: default;
  transition: all 0.25s !important;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  p {
    font-size: 0.9rem;
  }
}

a, i {
  color: #404040;
  cursor: pointer;
}

i {
  font-size: 1.5rem !important;
}

nav {
  position: fixed;
  z-index: 10;
  width: 1366px;
}

nav .nav-left {
  text-align: left;
}

nav .nav-right {
  float: right;
}

nav ul {
  margin: 0%;
  width: 100%;
  height: 7.5vh;
  list-style: none;
  height: auto;
  box-sizing: border-box;
}

nav li {
  cursor: pointer;
  text-transform: uppercase;
  display: inline-block !important;
  padding: 3% 2% 0.75% 2%;
  border-bottom: transparent 2px solid;
}

nav li a {
  letter-spacing: 0.15rem;
}

nav li:hover a, nav li:hover i {
  color: #309c8d;
}

nav li:hover {
  border-color: #309c8d;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  nav {
    width: 966px;
  }
}

.container {
  position: relative;
  max-width: 1366px;
  margin: 0% auto 0% auto !important;
  box-sizing: border-box;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  .container {
    max-width: 966px;
  }
}

.cta-button {
  letter-spacing: 0.15rem;
  font-size: 0.8rem;
  border: 1px solid #b5b5b5;
  padding: 0.75% 1.25% 0.75% 1.25%;
  color: #404040;
}

.cta-button:hover {
  border-color: #309c8d;
  color: #309c8d;
}

.content {
  width: 96.25%;
  margin: 0% auto 0% auto;
  padding: 0%;
  margin-bottom: 0%;
  position: relative;
}

.content h5 {
  font-size: 1.5rem;
  color: #666666;
}

.content section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.content #home * {
  position: absolute;
}

.content #home h1 {
  left: 0%;
  top: 31%;
  font-size: 8.5rem;
  letter-spacing: 0.25rem;
}

.content #home h1:hover {
  color: #309c8d;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  .content #home h1 {
    font-size: 5.5rem;
  }
}

.content #home h5 {
  left: 0.75%;
  top: 51%;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  .content #home h5 {
    font-size: 1.25rem;
  }
}

.content #home img {
  top: 17.5%;
  right: 0%;
}

@media (min-width: 1024px) and (max-width: 1550px) {
  .content #home img {
    width: 350px;
  }
}

.content #home .cta-button {
  left: 0.75%;
  top: 61%;
}

.box-wrapper {
  width: inherit;
  display: flex;
  justify-content: space-between;
  align-items: top;
}

.about-box {
  width: 30%;
}

.about-img {
  width: 100%;
  height: 30vh;
  background-color: gray;
  margin-bottom: 7.5%;
}

#userinfo {
  text-align: center;
  position: fixed;
  top: 20%;
  width: 15%;
}

#userinfo h5 {
  font-size: 1.25rem;
  text-align: center;
  margin: 25px 0 25px 0;
}

#userinfo .cta-button {
  padding: 5% 24% 5% 24%;
}
